@import 'mixins';

.top__bar {
  padding-block: 24px;
  @include content-padding-inline;
  
  display: flex;
  justify-content: space-between;
  align-items: center;


  @include onTablet() {
    padding: 36px 72px;
  }

  @include onPC() {
    padding: 36px 120px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }
  &__logo {
    @media (min-width: 320px) {
      height: 38px;
    }

    @media (min-width: 576px) {
      height: 56px;
    }

    @media (min-width: 576px) {
      height: 56px;
    }

    display: flex;
    overflow-y: auto;

    &:hover {
      transition: transform 0.3s;
      transform: scale(1.2);
    }
  }
}
