@mixin onTablet {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin onPC {
  @media (min-width: 1200px) {
    @content;
  }
}
@mixin grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet() {
    --columns: 6;

    column-gap: 24px;
  }

  @include onPC() {
    --columns: 12;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include onTablet() {
    padding-inline: 72px;
  }

  @include onPC() {
    padding-inline: 120px;
  }
}
